<template>
  <div id="app" :class="$store.state.platform">
    <!--<transition name="router-fade" mode="out-in" v-if="!$route.meta.nokeepAlive">
      <keep-alive>
      <router-view></router-view>
      </keep-alive>
    </transition>
    <transition  name="router-fade" v-else>
        <router-view></router-view>
    </transition>-->
    <keep-alive v-if="!$route.meta.nokeepAlive">
      <router-view></router-view>
    </keep-alive>
    <router-view v-else></router-view>


    <el-amap vid="amap" :zoom="16" :plugin="plugin" style="display: none" class="amap">

    </el-amap>
    <transition name="showalert">
      <v-alerttip v-if="showAlert"  @sureTip="$store.state.alertFn();$store.state.showAlert = false;">
        <div class="con" slot="info">
          <p class="tit">{{alertMessage}}</p>
        </div>
      </v-alerttip>
    </transition>

  </div>
</template>

<script>
  import * as mobile from './config/mUtils'
  import * as getData from './service/getData'
  import {mapState, mapMutations,mapActions} from 'vuex'
  import { lazyAMapApiLoaderInstance } from 'vue-amap'
  export default {
    name: 'app',
    data(){
      let self = this;
      return{
        lng:'',
        lat:'',
        add:'',
        plugin: [{
          pName: 'Geolocation',
          events: {
            init(o) {

              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  self.$nextTick();
                  var city;
                  city = `${result.addressComponent.city}-${result.addressComponent.district}-${result.addressComponent.neighborhood}`

                  self.SET_LOCATION({locationCity:result.addressComponent.city,city:result.addressComponent.city,add:city,lng:result.position.lng,lat:result.position.lat,citycode:result.addressComponent.citycode})
                  self.SET_LOCATIONCITY({locationCity:result.addressComponent.city,locationCitycode:result.addressComponent.citycode})
                }
              });
            }
          }
        }]
      }
    },
    mounted () {

      //this.appShow()
    },
    computed: {
      ...mapState([
        'activeCity',
        'activeLocation',
        'showAlert',
        'alertMessage'
      ])
    },
    methods: {
      ...mapMutations([
        'SET_LOCATION',
        'SET_LOCATIONCITY'
      ]),
      appShow () {
        const { PAGE_START_TIME } = window
        const END_TIME = new Date().getTime() // 结束时间
        const diffTime = END_TIME - PAGE_START_TIME
        const timer = setTimeout(() => {
          clearTimeout(timer)
          document.querySelector('.app-loading').className += ' app-loading-hide'
        }, diffTime > 2000 ? 0 : 2000 - diffTime)
      }
    }
  }

</script>

<style lang="scss">
  @import 'assets/css/base';
  @import 'assets/font/iconfont.css';
  .amap{
    position: absolute;
    width: 100%; height: 100%;
    z-index: 500;
  }
  /*.router-fade-enter-active, .router-fade-leave-active {
    transition: opacity .3s;
  }
  .router-fade-enter, .router-fade-leave-active {
    opacity: 0;
  }*/
</style>




